import { Component, OnInit } from '@angular/core';
import { SharedService } from "./../shared.service";
 
@Component({
  selector: 'app-movie',
  templateUrl: './movie.component.html',
  styles: []
})
export class MovieComponent implements OnInit {
    id_movie: string = "";
    mv_Title: string = "";
    mv_Rated: string = "";
    mv_Released: string = "";
    mv_image: string = "";
    mv_Plot: string = "";
    count:number ;
    constructor(public _sharedService: SharedService) {
    }
 
    ngOnInit() {
    }
 
    callMovieService() { 
        this._sharedService.findMovie(this.id_movie)
            .subscribe(
            lstresult => { 
                this.mv_Title = lstresult["results"][0]["title"];
                this.mv_Rated = lstresult["results"][0]["vote_count"]; 
                this.mv_Released = lstresult["results"][0]["release_date"];
                this.mv_image = lstresult["results"][0]["poster_path"];                
                this.mv_Plot = lstresult["results"][0]["overview"];
                this.count = lstresult["total_results"]
            },
            error => {
                console.log("Error. The findMovie result JSON value is as follows:");
                console.log(error);
            }
            ); 
    }
}


